"use client";
import React, { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import SkincareLanding from "./skincare-landing";
import ModestLanding from "./modest-landing";
import Ecomlanding from "./ecommerce-landin";
import RestLanding from "./restaurant-landing";
import MaskedText from "./masked-text";

gsap.registerPlugin(ScrollTrigger);

const slides = [
  {
    id: 1,
    bgColor: "bg-indigo-900",
    title: "Creative Portfolio",
    subtitle: "Web Developer & UI/UX Designer",
    description: "Creating digital experiences that connect and inspire",
  },
  {
    id: 2,
    bgColor: "bg-purple-900",
    title: "Featured Projects",
    subtitle: "Innovative Solutions",
    description: "Discover my latest work and creative process",
  },
  {
    id: 3,
    bgColor: "bg-blue-900",
    title: "Technical Skills",
    subtitle: "Expert Technologies",
    description: "Frontend, backend, and everything in between",
  },
  {
    id: 4,
    bgColor: "bg-pink-900",
    title: "Let's Connect",
    subtitle: "Work Together",
    description: "Ready to bring your vision to life",
  },
  {
    id: 5,
    bgColor: "bg-pink-900",
    title: "Let's Connect",
    subtitle: "Work Together",
    description: "Ready to bring your vision to life",
  },
];

const AnimatedSlider = () => {
  const containerRef = useRef(null);
  const slidesRef = useRef([]);
  const mainTimelineRef = useRef(null);
  const contextRef = useRef(null);

  useEffect(() => {
    if (!containerRef.current) return;

    const totalSlides = slides.length;

    // Create GSAP context to manage all animations
    contextRef.current = gsap.context(() => {
      // Set widths dynamically
      gsap.set(slidesRef.current, { width: "100vw", height: "100vh" });

      // Create sections for each slide
      const sections = slidesRef.current.filter(Boolean);

      // Create the main timeline
      const tl = gsap.timeline({
        scrollTrigger: {
          trigger: containerRef.current,
          pin: true,
          anticipatePin: 1,
          scrub: 1,
          snap: {
            snapTo: 1 / (totalSlides - 1),
            duration: 0.5,
            ease: "power2.inOut",
          },
          end: () => `+=${window.innerHeight * (totalSlides - 1)}`,
          onRefresh: () => {
            // Refresh content animations when ScrollTrigger refreshes
            sections.forEach((slide, index) => {
              if (index === 0 || !slide) return;
              const content = slide.querySelector(".slide-content");
              if (content) {
                gsap.set(content, {
                  opacity: index === 0 ? 1 : 0,
                  y: index === 0 ? 0 : 30,
                });
              }
            });
          },
        },
      });

      // Store the main timeline reference
      mainTimelineRef.current = tl;

      // Animate horizontal scroll
      tl.to(sections, {
        xPercent: -100 * (totalSlides - 1),
        ease: "none",
        duration: totalSlides - 1,
      });

      // Make the first slide immediately visible
      const firstSlideContent = sections[0]?.querySelector(".slide-content");
      if (firstSlideContent) {
        gsap.set(firstSlideContent, { opacity: 1, y: 0 });
      }

      // Animate content inside each slide
      sections.forEach((slide, index) => {
        if (index === 0 || !slide) return;

        const content = slide.querySelector(".slide-content");
        if (!content) return;

        // Set initial state
        gsap.set(content, { opacity: 0, y: 30 });

        // Create content animation
        ScrollTrigger.create({
          trigger: slide,
          containerAnimation: tl,
          start: "left center-=10%",
          end: "right center-=40%",
          toggleActions: "play none none reverse",
          animation: gsap.fromTo(
            content,
            { opacity: 0, y: 30 },
            {
              opacity: 1,
              y: 0,
              duration: 0.5,
              ease: "power2.out",
            }
          ),
        });
      });
    }, containerRef);

    // Handle resize with debounce
    let resizeTimeout;
    const handleResize = () => {
      clearTimeout(resizeTimeout);
      resizeTimeout = setTimeout(() => {
        ScrollTrigger.refresh(true);
      }, 100);
    };

    window.addEventListener("resize", handleResize);

    // Cleanup function
    return () => {
      clearTimeout(resizeTimeout);
      window.removeEventListener("resize", handleResize);

      // Kill ScrollTriggers first
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill());

      // Revert GSAP context safely
      if (contextRef.current) {
        contextRef.current.revert();
        contextRef.current = null;
      }

      // ✅ Only clear the refs after GSAP has finished cleanup
      slidesRef.current = [];
    };
  }, []);

  return (
    <div
      ref={containerRef}
      className="relative w-screen h-screen overflow-hidden"
    >
      <div className="flex w-max h-full">
        {slides.map((slide, index) => (
          <section
            key={slide.id}
            ref={(el) => {
              slidesRef.current[index] = el || null;
            }}
            className="w-screen h-screen flex items-center justify-center"
          >
            <div
              className={`slide-content text-white w-full h-full ${
                index === 0 ? "opacity-100" : "opacity-0"
              }`}
            >
              <div
                className={`${index === 4 ? "block" : "hidden"} w-full h-full`}
              >
                <SkincareLanding />
              </div>
              <div
                className={`${index === 1 ? "block" : "hidden"} w-full h-full`}
              >
                <ModestLanding />
              </div>
              <div
                className={`${index === 2 ? "block" : "hidden"} w-full h-full`}
              >
                <Ecomlanding />
              </div>
              <div
                className={`${index === 3 ? "block" : "hidden"} w-full h-full`}
              >
                <RestLanding />
              </div>
              <div
                className={`${
                  index === 0 ? "flex" : "hidden"
                } h-full w-full bg-white text-black items-center justify-center px-10`}
              >
                <MaskedText
                  text="Every detail earns its place. Landing pages that feel right — and work even better."
                  className="text-7xl leading-16"
                  indent="false"
                />
              </div>
            </div>
          </section>
        ))}
      </div>
    </div>
  );
};

export default AnimatedSlider;
